import React from "react";
import "../sources/css/scanner.css";
import Modal from "../commons/Modal_search";
import axios from "axios";
import { json } from "react-router";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { getAuthToken } from "../util/auth";
import PopUp from "../commons/PopUp"; 
export default function Release({scanner}) {
    /*QR    */
    const [outcomeModalOpen, setOutcomeModalOpen] = useState(false);
    const [discardModalOpen, setDiscardModalOpen] = useState(false);
    const [outcomeQrvalue, setOutcomeQrvalue] = useState('');
    const [discardQrvalue, setDiscardQrvalue] = useState('');
    const branch_id = localStorage.getItem("branch_id");
    const navigate = useNavigate();

    useEffect(() => {
        if (scanner === "outcome") {
            setOutcomeModalOpen(true);
        } else if (scanner === "discard") {
            setDiscardModalOpen(true);
        }
    }, [scanner]);

     //////////////////////////////////////////////////////////////////////
    /**/
    const [comment, setComment] = useState('');
    const [popupType, setPopupType] = useState('');
    const [isPopUpOpen, setPopUpOpen] = useState(false);
    const openPopUp = (type,comment) => {
        setPopUpOpen(true);
        setComment(comment);
        setPopupType(type);
    };
    const closePopUp = () => {
        setPopUpOpen(false);
        navigate('/branch/stock/inventory/list');
    };
    //////////////////////////////////////////////////////////////////////
    
    //QR 
    const handleModalClose = () => {
        setOutcomeQrvalue('');
        setDiscardQrvalue('');
        setOutcomeModalOpen(false);
        setDiscardModalOpen(false);
        navigate('/branch/sale/product');
    }

    // 
    const handleOutcomeModalOpen = () => {
        setOutcomeModalOpen(true);
    };

    const handleDiscardModalOpen = () => {
        setDiscardModalOpen(true);
    };

    //
    const handleOutcomeQRValue = (result) => {
        /* QR   */
        console.log("handleOutcomeQRValue ( QR) : ", result);
        // QR  QR  , TODO    
        if (result.includes('@') && result.includes('-')) {
            //   QR   
            setOutcomeQrvalue(result);
            setOutcomeModalOpen(false);
        } else {
            //   QR  (ex. QR ,  QR )
            setOutcomeModalOpen(false);
            setOutcomeQrvalue('');
            openPopUp("check"," QR .");
        }
    };

    useEffect(() => {
        const fetchData = async () => {
            if (!outcomeQrvalue) {
                return;
            }

            try {
                const token = getAuthToken();
                const qrcode_value = outcomeQrvalue;
                const response = await axios.get(
                    `/ssgtarbucks_BE/api/v1/qrcode/outcome/product/${qrcode_value}`,
                    {
                        headers: {
                            'Content-Type': 'application/json',
                            'jwtauthtoken': token
                        }, params: {
                            branch_id: branch_id
                        },
                    }
                );

                console.log("response >>>>>>>>>>>..", response);

                if (response.status !== 200) {
                    throw json({ message: '  .' }, { status: 500 });
                }

                const resData = response.data;
                console.log("resData", resData);
                openPopUp("success"," .");
                setOutcomeModalOpen(false);
                setOutcomeQrvalue('');
                //window.location.reload();
                
            } catch (error) {
                setOutcomeModalOpen(false);
                setOutcomeQrvalue('');
                console.error("Error during fetchData:", error);
                navigate('/error', { state: { errorMessage: ' ' } });
            }
        };

        if (outcomeQrvalue) {
            fetchData();
            
        }

    }, [outcomeQrvalue, navigate]);

    //
    const handleDiscardQRValue = (result) => {
        /* QR   */
        console.log("handleDiscardQRValue ( QR) : ", result);
        // QR  QR  , TODO    
        if (result.includes('@') && result.includes('-')) {
            //   QR   
            setDiscardQrvalue(result);
            setDiscardModalOpen(false);
        } else {
            //   QR  (ex. QR ,  QR )
            setDiscardModalOpen(false);
            setDiscardQrvalue('');
            openPopUp("check",' QR .');
        }
    };

    useEffect(() => {
        const fetchData = async () => {
            if (!discardQrvalue) {
                return;
            }

            try {
                const token = getAuthToken();
                const qrcode_value = discardQrvalue;
                const response = await axios.get(
                    `/ssgtarbucks_BE/api/v1/qrcode/discard/product/${qrcode_value}`,
                    {
                        headers: {
                            'Content-Type': 'application/json',
                            'jwtauthtoken': token
                        }, params: {
                            branch_id: branch_id
                        },
                    }
                );

                console.log("response >>>>>>>>>>>..", response);

                if (response.status !== 200) {
                    throw json({ message: '  .' }, { status: 500 });
                }

                const resData = response.data;
                console.log("resData", resData);
                openPopUp("success"," .");
                setDiscardModalOpen(false);
                setDiscardQrvalue('');
                //window.location.reload();
            } catch (error) {
                setDiscardModalOpen(false);
                setDiscardQrvalue('');
                console.error("Error during fetchData:", error);
                navigate('/error', { state: { errorMessage: ' ' } });
            }
        };

        if (discardQrvalue) {
            fetchData();
        }
    }, [discardQrvalue, navigate]);





    return (
        <>
            {outcomeModalOpen && (
                <Modal
                    onCancel={handleModalClose}
                    onScan={handleOutcomeQRValue}
                    onType={" "}
                >
                </Modal>)
            }
            {discardModalOpen && (
                <Modal
                    onCancel={handleModalClose}
                    onScan={handleDiscardQRValue}
                    onType={" "}
                >
                </Modal>)
            }
            {isPopUpOpen && (
                <PopUp onClose={closePopUp} onComment={comment} onType={popupType} />
            )}
        </>

    )
}