import React, { useState } from "react";
import axios from 'axios';
import { useNavigate,Link } from 'react-router-dom';
import '../sources/css/findpwd.css'
import PopUp from "../commons/PopUp"; 

export default function FindPwd() {

    const [userId, setUserId] = useState('');
    const [userEmail, setUserEmail] = useState('');
    const [authCode, setAuthCode] = useState('');
    const [showAuthCodeInput, setShowAuthCodeInput] = useState(false);
    const [verificationResult, setVerificationResult] = useState(null);
    const navigate = useNavigate();
    //////////////////////////////////////////////////////////////////////
    /**/
    const [comment, setComment] = useState('');
    const [popupType, setPopupType] = useState('');
    const [isPopUpOpen, setPopUpOpen] = useState(false);
    const openPopUp = (type,comment) => {
        setPopUpOpen(true);
        setComment(comment);
        setPopupType(type);
    };
    const closePopUp = () => {
        setPopUpOpen(false);
        if(comment==="  ."){
            navigate('/');
        }
    };
    //////////////////////////////////////////////////////////////////////

    //  
    const handleFindButtonClick = async (event) => {

        if (userId === '' || userEmail === '') {
            openPopUp("check","   .");
            return;
        }

        try {
            const response = await axios({
                method: "POST",
                url: 'http://localhost:8000/api/v1/user/find',
                data: {
                    user_id: userId,
                    user_email: userEmail,
                    auth_code: authCode,
                },
            });

            console.log('Axios Response:', response.data);

            if (response.data === '') {
                console.log("  ");
                openPopUp("fail","    .\n   .");
            } else {
                console.log("  ");
                //  
                setShowAuthCodeInput(true);
            }

        } catch (error) {
            console.error('Axios Error:', error);
        }
    };

    const handleVerificationButtonClick = async () => {
        if (authCode === '') {
            openPopUp("check"," .");
            return;
        }

        try {
            console.log(authCode);

            const verificationResponse = await axios({
                method: "POST",
                url: 'http://localhost:8000/api/v1/user/verify',
                data: {
                    user_id: userId,
                    auth_code: authCode,
                },
            });

            console.log('Verification Response:', verificationResponse.data);

            if (verificationResponse.data === '') {
                //  
                //  
                openPopUp("success","  .");
                
            }else {
                openPopUp("fail","  .");
            }

            setVerificationResult(verificationResponse.data);

        } catch (error) {
            console.error('Verification Axios Error:', error);
        }
    };

    return (
        <>
        <div className="bg">
            <div>
                <div className="circle shadow-lg"></div>
                <div className="card shadow-lg">
                    <h2>SSGTARBUCKS</h2><br />
                    <h4> </h4>
                    <div className="form">
                        <input type="text" placeholder="" name="user_id" value={userId} onChange={(e) => setUserId(e.target.value)} />
                        <input type="e-mail" placeholder="" name="user_email" value={userEmail} onChange={(e) => setUserEmail(e.target.value)} />
                        <button type="submit" onClick={handleFindButtonClick}> </button>
                    </div>
                    <div className={showAuthCodeInput ? "form" : "form1"}>
                        <input type="text" placeholder="" value={authCode} onChange={(e) => setAuthCode(e.target.value)} />
                        <button onClick={handleVerificationButtonClick}> </button>
                    </div>
                    <footer>
                    <Link to="/"><button className="backbutton">BACK</button></Link>
                    </footer>
                </div>
            </div>
        </div>
        {isPopUpOpen &&(
            <PopUp onClose={closePopUp} onComment={comment} onType={popupType} />
        )}
        </>
    );
}